<template>
    <div class="rating_page">
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                        <span class="title_text">
                          {{this.$route.query.shop.name}}
                        </span>
            </section>
        </header>
        <section class="header_img">
            <img :src="'https://elm.cangdu.org/img/'+shop.image_path" alt="" class="food_img">
        </section>
        <p class="description">{{shop.description}}</p>
        <section class="detail_container">
            <section class="detail_left">
                <p>{{shop.name}}</p>
                <div class="rating_sale">
                    <span>评分</span>
                    <div class="rating_container">
                        <section class="star_container">
                            <el-rate
                                    v-model="shop.rating-0"
                                    disabled
                                    show-score
                                    text-color="#ff9900">
                            </el-rate>
                        </section>
                    </div>
                </div>
                <p>
                    <span>月售{{shop.month_sales}}</span>
                    <span>售价 ￥20</span>
                    <span>起</span>
                </p>
                <p>
                    <span>评论数&nbsp;{{shop.rating_count}}</span>&nbsp;
                    <span>好评率 &nbsp;{{shop.satisfy_rate}}%</span>
                </p>
            </section>
        </section>
        <section class="buy_cart_container">
            <section class="cart_icon_num">
                <div class="cart_icon_container" id="cart_icon_activity">
                    <span id="cart_list_length" v-if="count">{{count}}</span>
                    <i class="el-icon-shopping-cart-2"></i>
                </div>
                <div class="cart_num">
                    <div>￥{{price}}</div>
                    <div>配送费5元</div>
                </div>
            </section>
            <section class="gotopay" id="gotopay_acitvity" v-if="!show5">
                <span class="gotopay_button_style">还差20元起送</span>
            </section>
            <section class="gotopay" id="gotopay_acitvity1" v-else @click="goConfirmOrder()">
                <span class="gotopay_button_style">结算</span>
            </section>
        </section>
        <el-collapse-transition>
            <div v-if="!show7" class="cart_food_list">
                <header>
                    <h4>购物车</h4>
                    <div @click="deleteAll">
                        <i class="el-icon-delete"></i>
                        <span class="clear_cart">清空</span>
                    </div>
                </header>
                <section class="cart_food_details">
                    <ul>
                        <li class="cart_food_li" v-for="v in mycar" :key="v.name">
                            <div class="cart_list_num">
                                <p class="ellipsis">{{v.shop.name}}</p>
                                <p class="ellipsis">{{v.shop.specfoods[v.i].specs_name}}</p>
                            </div>
                            <div class="cart_list_price">
                                <span>￥</span>
                                <span >{{v.shop.specfoods[v.i].price}}</span>
                            </div>
                            <section class="cart_list_control">
                                <i class="el-icon-circle-plus" v-if="v.num" @click="addcar(v.shop,0)"></i>
                                <transition name = "right">
                                    <div class="remove" v-if="v.num">
                                        <span class="el-icon-remove-outline" @click="removecar(v.shop)"></span>
                                        <span style="margin:0 .3rem">{{v.num}}</span>
                                    </div>
                                </transition>

                            </section>
                        </li>
                    </ul>
                </section>
            </div>
        </el-collapse-transition>
    </div>
</template>

<script>
    export default {
        name: "FoodDetail",
        data(){
            return{
                shop:"",
                show7:true,
                show5:true,
                count:0,
                price:0,
            }
        },
        created(){
            this.shop = this.$route.query.shop
            // console.log(this.$route.query.shop);
            if(localStorage.getItem("mycar")){
                this.mycar = JSON.parse(localStorage.getItem("mycar"))
            }
            for (let i = 0; i <this.mycar.length;i++){
                let index = this.mycar[i].i
                this.count += this.mycar[i].num;
                this.price += (this.mycar[i].shop.specfoods[index].price*this.mycar[i].num);
                // console.log(this.price);
                // console.log(this.count)
            }
        },
        methods:{
            goBack(){
                this.$router.go(-1)
            },
            goConfirmOrder(){
                this.$router.push({
                    path:"/confirmOrder",
                })
            }
        }
    }
</script>

<style scoped>
    .rating_page{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #fff;
        z-index: 12;
        padding-top: 1.95rem;
    }
    #head_top{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .header_img{
        position: relative;
    }
    .food_img {
        width: 100%;
        display: block;
    }
    .description {
        font-size: .6rem;
        color: #666;
        margin-top: .5rem;
        line-height: .8rem;
        bottom: .2rem;
        padding: 0 .4rem;
    }
    .detail_container {
        padding: .5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .rating_sale{
        display: flex;
        align-items: center;
    }
    .rating_container {
        position: relative;
        top: .2rem;
        width: 3.5rem;
        height: .4rem;
    }
    .star_container {
        position: absolute;
        display: flex;
        width: 3.5rem;
        height: .4rem;
        top: -.02rem;
        left: -.02rem;
    }
    .rating_container .star_container  /deep/ .el-rate {
        width: 3.5rem;
        margin-top: -.5rem;
    }
    .rating_container .star_container .el-rate  /deep/ .el-rate__item{
        /*width: 2.5rem;*/
        margin-right: -.5rem;
    }
    .detail_left p:first-of-type {
        font-size: .7rem;
        color: #333;
        margin-bottom: .2rem;
    }
    .rating_sale span:first-of-type {
        font-size: .6rem;
        color: #666;
        margin-right: .2rem;
    }
    .detail_left p:nth-of-type(2) span:first-of-type {
        font-size: .6rem;
        color: #666;
        margin-right: .4rem;
    }
    .detail_container .detail_left p:nth-of-type(2) span:nth-of-type(2), .detail_container .detail_left p:nth-of-type(2) span:nth-of-type(3) {
        font-size: .6rem;
        color: #f60;
        margin-right: .2rem;
    }
    .detail_container .detail_left p:nth-of-type(2) {
        margin-top: .3rem;
    }
    .detail_left p:nth-of-type(3) span {
        font-size: .6rem;
        color: #666;
    }
    .buy_cart_container{
        position: absolute;
        background-color: #3d3d3f;
        bottom: 0;
        left: 0;
        z-index: 13;
        display: flex;
        width: 100%;
        height: 2rem;
    }
    .cart_icon_num {
        flex: 1;
    }
    .cart_icon_container{
        display: flex;
        background-color: #3d3d3f;
        position: absolute;
        padding: .4rem;
        border: .18rem solid #444;
        border-radius: 50%;
        left: .5rem;
        top: -.7rem;
    }
    .cart_icon_container i{
        font-size: 1.2rem;

    }
    .cart_num {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 3.5rem;
    }
    .cart_icon_num .cart_num div:first-of-type {
        font-size: .8rem;
        font-weight: 700;
        margin-bottom: .1rem;
    }
    .cart_icon_num .cart_num div:nth-of-type(2) {
        font-size: .4rem;
    }
    .buy_cart_container .cart_icon_num .cart_num div {
        color: #fff;
    }
    #cart_icon_activity{
        background-color: #3e97e9;
    }
    .gotopay {
        position: absolute;
        right: 0;
        background-color: #535356;
        width: 5rem;
        height: 100%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .gotopay_button_style{
        font-size: .7rem;
        color: #fff;
        font-weight: 700;

    }
    #gotopay_acitvity1{
        background-color: #4cd964
    }
    #cart_list_length{
        position: absolute;
        top: -.25rem;
        right: -.25rem;
        background-color: #ff461d;
        line-height: .7rem;
        text-align: center;
        border-radius: 50%;
        border: .025rem solid #ff461d;
        min-width: .7rem;
        height: .7rem;
        font-size: .5rem;
        color: #fff;
        font-family: Helvetica Neue,Tahoma,Arial;
    }
</style>